import React from "react";
import DocumentTitle from "react-document-title";

import ListTerm from './../../components/listterm/index.js';
import PageIndex from './../../widgets/pageindex/index.js';

import "./termspage.css";


export default class TermsPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: '/' });
    pageindex_array.push({ label: lang.termspage_title, value: '/terms' });

    return (
      <DocumentTitle title={`${lang.termspage_title}${lang.document_title}`}>
        <div className="termspage">
          <PageIndex data={pageindex_array} />
          <ListTerm />
        </div>
      </DocumentTitle>
    )
  }

}


