import React from "react";

import Section from './../../widgets/section/index.js';

import './listterm.css';

export default class TermsPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;
    return (
      <div className="listterm">
        <Section title={lang.termspage_section_change_title} className="listterm-term" titleClassName="dh-h3">
          <div dangerouslySetInnerHTML={{ __html: lang.termspage_section_change_content }} />
        </Section>

        <Section title={lang.termspage_section_cancel_title} className="listterm-term" titleClassName="dh-h3">
          <div dangerouslySetInnerHTML={{ __html: lang.termspage_section_cancel_content }} />
        </Section>

         <Section title={lang.termspage_section_weather_title} className="listterm-term" titleClassName="dh-h3">
          <div dangerouslySetInnerHTML={{ __html: lang.termspage_section_weather_content }} />
        </Section>

        <Section title={lang.termspage_section_rules_title} className="listterm-term" titleClassName="dh-h3">
          <div dangerouslySetInnerHTML={{ __html: lang.termspage_section_rules_content }} />
        </Section>


      </div>
    )
  }
}


