import React from "react";
import DocumentTitle from "react-document-title";

import PageIndex from "./../../widgets/pageindex/index.js";
import Table from "./../../widgets/tablelist/index.js";

import Slides from "./../../components/slides/index.js";
import Intro from "./../../components/intro/index.js";

import "./pricingpage.css";

export default class PricingPage extends React.Component {
  constructor(props) {
    super(props);
    this.common = window.common;
  }

  render() {
    var that = this;
    var { lang } = that.common.data;

    var pricingData = [];
    pricingData.push({
      timeing_title: lang.pricingpage_table_price_timeing_content1,
      price_title: lang.pricingpage_table_price_price_content1
    });
    pricingData.push({
      timeing_title: lang.pricingpage_table_price_timeing_content2,
      price_title: lang.pricingpage_table_price_price_content2
    });

    var pageindex_array = [];
    pageindex_array.push({ label: lang.homepage_title, value: "/" });
    pageindex_array.push({ label: lang.pricingpage_title, value: "/pricing" });
    return (
      <DocumentTitle title={`${lang.pricingpage_title}${lang.document_title}`}>
        <div className="pricingpage">
          <PageIndex data={pageindex_array} />

     
          <Intro
            title={lang.pricingpage_intro_title}
            description={lang.pricingpage_intro_description}
          />
  
          <div className="inner_wrapping">
            <Table
              records={pricingData}
              labelPrefix="pricingpage_table_price_"
            />
          </div>

          {/* <Intro
            title={lang.pricingpage_intro_title}
            description={lang.pricingpage_intro_description}
          />
  
          <div className="inner_wrapping">
            <Table
              records={pricingData}
              labelPrefix="pricingpage_table_price_"
            />
          </div> */}



        </div>
      </DocumentTitle>
    );
  }
}



